import { Component, OnInit } from '@angular/core';
import { AuthService } from "../../auth.service";
import { Injectable, NgZone } from '@angular/core';
import { AngularFireAuth } from "@angular/fire/auth";
import { AngularFirestore, AngularFirestoreDocument } from '@angular/fire/firestore';
import { Router } from "@angular/router";
@Component({
  selector: 'app-login-area',
  templateUrl: './login-area.component.html',
  styleUrls: ['./login-area.component.css']
})
export class LoginAreaComponent implements OnInit {
  hide: boolean = true
  forgot: boolean = false
  check: any;
  emails: any;
  password: any;
  OverLay:boolean = false
  errormessage;
  constructor(public AuthService: AuthService,
    public afs: AngularFirestore,   // Inject Firestore service
    public afAuth: AngularFireAuth, // Inject Firebase auth service
    public router: Router,  
    public ngZone: NgZone // NgZone service to remove outside scope warning
    ) { }

  ngOnInit(): void {
    const RemEmail = JSON.parse(localStorage.getItem('email'));
    const Rempassword = JSON.parse(localStorage.getItem('password'));

    if (RemEmail != null && Rempassword != null) {
      this.check = true
      this.emails = RemEmail
      this.password = Rempassword
    }
  }




  RememberMe(email, password, check) {
    if (check == undefined) {
      localStorage.setItem('email', JSON.stringify(email));
      localStorage.setItem('password', JSON.stringify(password));
    }
    else {
      localStorage.setItem('email', JSON.stringify(null));
      localStorage.setItem('password', JSON.stringify(null));
    }

  }

  Signup(email, password)
  {
    this.AuthService.SignUp(email, password)

  }
  login(email, password) {
console.log(email)
if(email == undefined || password == undefined)
{
  this.errormessage = "Please enter email and password"
  this.OverLay = true
}
else
{
    return this.afAuth.signInWithEmailAndPassword(email, password)
    .then((result) => {
      this.ngZone.run(() => {
        localStorage.setItem('Loginemail', JSON.stringify(email));
        localStorage.setItem('Loginpassword', JSON.stringify(password));
       window.location.reload();
      });


    }).catch((error) => {
      this.errormessage = error.message
     this.OverLay = true
    })

  }
  }

  Forgot(email) {
    this.AuthService.ForgotPassword(email)
    this.forgot = false
  }

  ForgotOverLay() {
    this.forgot = true
  }
  closePopUp() {
    this.forgot = false
  }
}
