import { templateJitUrl } from '@angular/compiler';
import { Component, OnInit } from '@angular/core';
import { Router, ActivatedRoute, ParamMap } from "@angular/router";
import {AddUserService} from "../../Services/add-user.service"
import { Location } from '@angular/common';

@Component({
  selector: 'app-user-detail-result',
  templateUrl: './user-detail-result.component.html',
  styleUrls: ['./user-detail-result.component.css','../Admin-common.css','../../Commoncss/popup.css']
})
export class UserDetailResultComponent implements OnInit {
editDetail:boolean = false
fullname:any 
companyname:any 
email:any
nameWebiste:any 
linkwebsite:any 
webDate:any 
  sub: any;
  docid: string;
  Name: any;
  CompanyName: any;
  website: any;
  startdate: any;
  CutomerDetail;
  OverLay:boolean = false
  errors :boolean = false
  constructor(public ActivatedRoute:ActivatedRoute,public AddUserService:AddUserService,public _location:Location) { }

  ngOnInit(): void {
    this.sub = this.ActivatedRoute.paramMap.subscribe((params) => {
      this.docid =  params.get("id")
    })
    this.getUserDetail()
  }




  getUserDetail()
  {
    this.AddUserService.getselectedUsers(this.docid).then((element)=>{
      this.fullname = element.data().fullname
      this.CompanyName = element.data().CompanyName
      this.email = element.data().email
      this.website = element.data().website
      this.linkwebsite = element.data().linkwebsite
      this.startdate = element.data().startdate.toDate()
    })
  }

 Update()
  {

    this.CutomerDetail = {
      fullname:this.fullname,
      CompanyName: this.CompanyName,
      email: this.email,
      website: this.website,
      linkwebsite: this.linkwebsite,
      startdate: this.startdate,
      Archieve: "false",
      user:'user'
    }
    this.AddUserService.updateLeads(this.docid,this.CutomerDetail).then(element => {
     this.OverLay = true
     this.getUserDetail()
    })
  }
  editDetailUser()
  {
   this.editDetail = true
  }
  backeditDetailUser()
  {
   this.editDetail = false
  }
  backed() {
    //this._location.back()
    this.OverLay = false
    this.editDetail = false
    this.getUserDetail()
  }

  checkUserFields()
  {
  console.log(this.fullname.length == 0)
    if(this.fullname.length == 0 || this.CompanyName.length == 0 || this.email.length == 0 || this.website.length == 0 || this.linkwebsite.length == 0 || this.startdate.length == 0 )
  {
  this.errors = true
  }
  else
  {
    this.Update()
  }
  
}
}
