import { Component, OnInit } from '@angular/core';
import {AuthService} from '../auth.service'
import {AddUserService} from "../Services/add-user.service"
import { map, startWith, take, ignoreElements } from "rxjs/operators";
@Component({
  selector: 'app-admin-panel',
  templateUrl: './admin-panel.component.html',
  styleUrls: ['./admin-panel.component.css', './Admin-common.css']
})
export class AdminPanelComponent implements OnInit {
  notLoggedIn:boolean = false
  totalNumberofUser = [];
  matchesValue: any;
  openTickets =[];
  closedTickets =[]
  detelelement;
  lengthopen
  Tickets : boolean = false
  CloseTickets:boolean = false
  constructor(public AuthService:AuthService,public AddUserService:AddUserService) { }

  ngOnInit(): void {
    const UserDetail =  JSON.parse(localStorage.getItem('user'));

    if( UserDetail != null)
    {
      this.notLoggedIn = true
    }


    this.AddUserService.getUser().then((ref)=>{
      ref.forEach(element => {
        this.detelelement = element.data() 
        if(this.detelelement.user  == 'user')
        {
          this.totalNumberofUser.push(element.data())
        }
        
      });
    })
    this.getAllTickets()
  }



getAllTickets()
{

  this.AddUserService.getAllTickets().snapshotChanges().pipe(
    map(changes =>
      changes.map(c =>
        ({ id: c.payload.doc.id, ...c.payload.doc.data() })
      )
    )
  ).subscribe(data => {
    data.forEach((datas) => {
      this.matchesValue = datas      
      this.openTickets.push(datas )
      this. lengthopen =  this.openTickets.length
      // if(this.matchesValue.Archived == 'false' )
      // {
      
      // }
      // if(this.matchesValue.Archived == 'true')
      // {
      //   this. closedTickets.push(datas)
      // }
    })
    this.openTickets = this.openTickets.filter((test, index, array) =>
     index === array.findIndex((findTest) =>
        findTest.id=== test.id
     )
 

     
  );
  this.closedTickets = this.closedTickets.filter((test, index, array) =>
  index === array.findIndex((findTest) =>
     findTest.id=== test.id
  ));


  });
}
OpenTickets()
{
  this.Tickets = true
  this.CloseTickets =false

}

closeTickets()
{
  this.CloseTickets = true
  this.Tickets = false
}

  
}
