import { Component, OnInit } from '@angular/core';
import {AuthService} from '../../auth.service'
import { Router, ActivatedRoute, ParamMap } from "@angular/router";
@Component({
  selector: 'app-admin-header',
  templateUrl: './admin-header.component.html',
  styleUrls: ['./admin-header.component.css']
})
export class AdminHeaderComponent implements OnInit {
UserPanel:boolean = false
dashboard:boolean = false
Archieve :boolean = false
  origin: string;
  logo:boolean = false
  constructor(public AuthService:AuthService,public Router:Router) { }

  ngOnInit(): void {
    this.origin = window.location.origin + "/#"
    if(window.location.href ==  this.origin +"/")
    {
      this.dashboard = true
      this.UserPanel = false
      this.Archieve = false
      this.logo = false
    }
    if(window.location.href == this.origin + "/UserPanel")
    {
      this.dashboard = false
      this.UserPanel = true
      this.Archieve = false
      this.logo = false
    }

    if(window.location.href == this.origin + "/Archieve")
    {
      this.dashboard = false
      this.UserPanel =false
      this.Archieve = true
      this.logo = false

    }
    if(window.location.href !=  this.origin +"/" && window.location.href != this.origin + "/UserPanel" && window.location.href != this.origin + "/Archieve")
    {
      this.logo = true
      this.dashboard = false
      this.UserPanel = true
      this.Archieve = false
   
    }
    
  }


  LogOut()
  {
    this.AuthService.SignOut()
  }

  routeLogo()
  {
    console.log('tets')
  this.Router.navigate(['/UserPanel'])
  }
}
