@Component({
  selector: 'app-payments',
  templateUrl: './payments.component.html',
  styleUrls: ['./payments.component.css','../Admin-common.css','../../Commoncss/popup.css','../../Commoncss/imageslider.css']
})
export class PaymentsComponent implements OnInit {
  Amount;
  ModeofPayment;
  date;
  Payments
  sub: any;
  docid: string;
  OverlayMessage;
  loader:boolean = false
  OverLay:boolean = false
  Allpayments =[]
  TimeLineDetails;
  Now
  interval
  constructor(public AddUserService:AddUserService,public ActivatedRoute:ActivatedRoute) { }

  ngOnInit(): void {
    this.sub = this.ActivatedRoute.paramMap.subscribe((params) => {
      this.docid =  params.get("id")
    })

    this.getAllPayments()

  //   this.interval = setInterval(() => { 
          
  //     this.AddUserService.getPayment(this.docid).then((ref)=>{
  //       ref.forEach(element => {
  //         this.Allpayments.push({Detail:element.data(),id:element.id})
  //       });
  //       this.Allpayments.sort(function (a, b) { return a.Detail.date.seconds - b.Detail.date.seconds });
     
  //       this.Allpayments =  this.Allpayments.filter((test, index, array) =>
  //       index === array.findIndex((findTest) =>findTest.id=== test.id));
  //     })
  
  // }, 3000);
  }





  AddPayment()
  {
    this.OverlayMessage = "Adding payment"
    this.loader = true
    this.Payments = {
      Amount:this.Amount,
      ModeofPayment:this.ModeofPayment,
      date:this.date
    }
    this.AddUserService.CreatePaymennt(this.docid,this.Payments).then((element)=>{
      this.loader = false
      this.OverLay = true
      this.createNotification()
      this.getAllPayments()
    })
  }

  ClosePopup()
  {
    this.OverLay = false
  }

  getAllPayments()
  {
    this.Allpayments = []
    this.OverlayMessage = "Getting  payments"
 this.loader = true
    this.AddUserService.getPayment(this.docid).then((ref)=>{
      ref.forEach(element => {
        this.Allpayments.push({Detail:element.data(),id:element.id})

      });
      this.Allpayments.sort(function (a, b) {
        return a.Detail.date.seconds - b.Detail.date.seconds
      });
      this.loader = false
    })
  }

  createNotification()
{
  this.Now = new Date();
  const Tickets = "Payment"
  this.TimeLineDetails ={
    Amount:this.Amount,
    ModeofPayment:this.ModeofPayment,
    date:this.date,
    close:null,
    Now:this.Now 
  }
  this.AddUserService.CreateNoti(this.docid,Tickets,this.TimeLineDetails)
}
}
