import { Component, OnInit } from '@angular/core';
import { Router, ActivatedRoute, ParamMap } from "@angular/router";

@Component({
  selector: 'app-user-detail',
  templateUrl: './user-detail.component.html',
  styleUrls: ['./user-detail.component.css']
})
export class UserDetailComponent implements OnInit {
 userDetail:boolean = true
 payments:boolean = false
 isLoading:boolean = false
 tickets:boolean = false
 timeLine:boolean = false
  sub: any;
  docid: string;
  constructor(public ActivatedRoute:ActivatedRoute,public  Router: Router) { }

  ngOnInit(): void {
    const lastItem = window.location.pathname.substring(window.location.pathname.lastIndexOf('/#/') + 1)
console.log( window.location.hash)
    document.getElementById("Detail").style.background = "white"
    document.getElementById("timeline").style.background = "#E8E8E8"
    document.getElementById("tickets").style.background = "#E8E8E8"
    document.getElementById("Payment").style.background = "#E8E8E8"
    document.getElementById("Detail").style.border = "none";
    document.getElementById("timeline").style.border = "1px solid #707070";
    document.getElementById("tickets").style.border = "1px solid #707070";
    document.getElementById("Payment").style.border = "1px solid #707070";
    document.getElementById("Detail").style.borderTop = "3px solid #E8E8E8";

    // window.location.hash.includes("User")
this.sub = this.ActivatedRoute.paramMap.subscribe((params) => {
  this.docid =  params.get("id")
})
if(window.location.hash.includes("User") == true)
{
  this.userTab()
}
if(window.location.hash.includes("time") == true)
{
  this.time()
}
if(window.location.hash.includes("ticket") == true)
{
  this.tickettab()
}
if( window.location.hash.includes("payment") == true)
{
  this.paymentTab()
}


  }


  

  userTab()
  {
    this.Router.navigate(['/UserDetail/'+ this.docid + '/User'])
    this.userDetail = true
    this.payments  =false
    this.tickets = false
    this.timeLine = false
    document.getElementById("Detail").style.background = "white"
    document.getElementById("timeline").style.background = "#E8E8E8"
    document.getElementById("tickets").style.background = "#E8E8E8"
    document.getElementById("Payment").style.background = "#E8E8E8"
    document.getElementById("Detail").style.border = "none";
    document.getElementById("timeline").style.border = "1px solid #707070";
    document.getElementById("tickets").style.border = "1px solid #707070";
    document.getElementById("Payment").style.border = "1px solid #707070";
    document.getElementById("Detail").style.borderTop = "3px solid #E8E8E8";
  }





  paymentTab()
  {
    this.Router.navigate(['/UserDetail/'+ this.docid + '/payment'])
    this.userDetail = false
    this.payments  = true
    this.tickets = false
    this.timeLine = false
    document.getElementById("Payment").style.border = "none";
    document.getElementById("Payment").style.background = "white"
    document.getElementById("Detail").style.background = "#E8E8E8"
    document.getElementById("Detail").style.border = "1px solid #707070";
    document.getElementById("timeline").style.background = "#E8E8E8"
    document.getElementById("tickets").style.background = "#E8E8E8"
    document.getElementById("timeline").style.border = "1px solid #707070";
    document.getElementById("tickets").style.border = "1px solid #707070";
    document.getElementById("Payment").style.borderTop = "3px solid #E8E8E8";
  }

tickettab()
{
  this.Router.navigate(['/UserDetail/'+ this.docid + '/ticket'])
  this.userDetail = false
    this.payments  = false
    this.tickets = true
    this.timeLine = false
    document.getElementById("Payment").style.border = "1px solid #707070";
    document.getElementById("Payment").style.background = "#E8E8E8"
    document.getElementById("Detail").style.background = "#E8E8E8"
    document.getElementById("Detail").style.border = "1px solid #707070";
    document.getElementById("timeline").style.background = "#E8E8E8"
    document.getElementById("tickets").style.background = "white"
    document.getElementById("timeline").style.border = "1px solid #707070";
    document.getElementById("tickets").style.border = "none";
    document.getElementById("tickets").style.borderTop = "3px solid #E8E8E8";

}

time()
{
  this.Router.navigate(['/UserDetail/'+ this.docid + '/time'])
  this.userDetail = false
    this.payments  = false
    this.tickets = false
    this.timeLine = true
    document.getElementById("Payment").style.border = "1px solid #707070";
    document.getElementById("Payment").style.background = "#E8E8E8"
    document.getElementById("Detail").style.background = "#E8E8E8"
    document.getElementById("Detail").style.border = "1px solid #707070";
    document.getElementById("timeline").style.background = "white"
    document.getElementById("tickets").style.background = "#E8E8E8"
    document.getElementById("timeline").style.border = "none";
    document.getElementById("tickets").style.border = "1px solid #707070";
    document.getElementById("timeline").style.borderTop = "3px solid #E8E8E8";
}

}
